/**
 * @hua-labs/hua/framework - useFocusTrap
 * 
 * ,     hook
 * Traps focus within a container (e.g., modal, drawer)
 */

'use client';

import { useEffect, useRef, useCallback, type RefObject } from 'react';

/**
 * Focus Trap 
 */
export interface FocusTrapOptions {
  /**
   *    
   * Whether focus trap is active
   * 
   * @default true
   */
  isActive?: boolean;
  
  /**
   * Escape     
   * Callback when Escape key is pressed
   */
  onEscape?: () => void;
  
  /**
   *    
   * Selector for initial element to focus
   */
  initialFocus?: string;
  
  /**
   *       
   * Callback when focus leaves the trap
   */
  onFocusOut?: () => void;
}

/**
 * Focus Trap Hook
 * 
 * ,      .
 * Traps focus within a container (e.g., modal, drawer).
 * 
 * @example
 * ```tsx
 * function Modal({ isOpen, onClose }) {
 *   const modalRef = useFocusTrap({ 
 *     isActive: isOpen, 
 *     onEscape: onClose 
 *   });
 * 
 *   if (!isOpen) return null;
 * 
 *   return (
 *     <div ref={modalRef} role="dialog" aria-modal="true">
 *       <button onClick={onClose}>Close</button>
 *       Modal content
 *     </div>
 *   );
 * }
 * ```
 * 
 * @param options - Focus Trap 
 * @returns   ref
 */
export function useFocusTrap<T extends HTMLElement = HTMLElement>(
  options: FocusTrapOptions = {}
): RefObject<T | null> {
  const {
    isActive = true,
    onEscape,
    initialFocus,
    onFocusOut,
  } = options;

  const ref = useRef<T>(null);
  const previousActiveElement = useRef<HTMLElement | null>(null);
  const onEscapeRef = useRef(onEscape);
  const onFocusOutRef = useRef(onFocusOut);

  //  ref 
  useEffect(() => {
    onEscapeRef.current = onEscape;
    onFocusOutRef.current = onFocusOut;
  }, [onEscape, onFocusOut]);

  useEffect(() => {
    if (!isActive || !ref.current) {
      return;
    }

    const container = ref.current;

    //    
    previousActiveElement.current = document.activeElement as HTMLElement;

    //    
    const getFocusableElements = (): HTMLElement[] => {
      const focusableSelectors = [
        'a[href]',
        'button:not([disabled])',
        'textarea:not([disabled])',
        'input:not([disabled])',
        'select:not([disabled])',
        '[tabindex]:not([tabindex="-1"])',
      ].join(', ');

      return Array.from(container.querySelectorAll<HTMLElement>(focusableSelectors))
        .filter((el) => {
          //   
          const style = window.getComputedStyle(el);
          return style.display !== 'none' && style.visibility !== 'hidden';
        });
    };

    //   
    const focusInitialElement = () => {
      try {
        if (initialFocus) {
          const element = container.querySelector<HTMLElement>(initialFocus);
          if (element) {
            element.focus();
            return;
          }
        }

        //      
        const focusableElements = getFocusableElements();
        if (focusableElements.length > 0) {
          focusableElements[0].focus();
        } else {
          //      
          container.setAttribute('tabindex', '-1');
          container.focus();
        }
      } catch (error) {
        // focus()   (:    )
        //   (    )
        if (process.env.NODE_ENV === 'development') {
          console.warn('[useFocusTrap] Failed to focus initial element:', error);
        }
      }
    };

    //   
    const handleKeyDown = (event: KeyboardEvent) => {
      if (event.key === 'Escape' && onEscapeRef.current) {
        event.preventDefault();
        onEscapeRef.current();
        return;
      }

      // Tab  
      if (event.key === 'Tab') {
        const focusableElements = getFocusableElements();
        if (focusableElements.length === 0) {
          event.preventDefault();
          return;
        }

        const firstElement = focusableElements[0];
        const lastElement = focusableElements[focusableElements.length - 1];
        const currentElement = document.activeElement as HTMLElement;

        // Shift + Tab ()
        if (event.shiftKey) {
          if (currentElement === firstElement) {
            event.preventDefault();
            try {
              lastElement.focus();
            } catch (error) {
              // focus()    
              if (process.env.NODE_ENV === 'development') {
                console.warn('[useFocusTrap] Failed to focus last element:', error);
              }
            }
          }
        } else {
          // Tab ()
          if (currentElement === lastElement) {
            event.preventDefault();
            try {
              firstElement.focus();
            } catch (error) {
              // focus()    
              if (process.env.NODE_ENV === 'development') {
                console.warn('[useFocusTrap] Failed to focus first element:', error);
              }
            }
          }
        }
      }
    };

    //   
    const handleFocusIn = (event: FocusEvent) => {
      if (!container.contains(event.target as Node)) {
        onFocusOutRef.current?.();
        //     
        try {
          const focusableElements = getFocusableElements();
          if (focusableElements.length > 0) {
            focusableElements[0].focus();
          }
        } catch (error) {
          // focus()    
          if (process.env.NODE_ENV === 'development') {
            console.warn('[useFocusTrap] Failed to refocus element:', error);
          }
        }
      }
    };

    //   
    focusInitialElement();

    //   
    container.addEventListener('keydown', handleKeyDown);
    document.addEventListener('focusin', handleFocusIn);

    //  
    return () => {
      container.removeEventListener('keydown', handleKeyDown);
      document.removeEventListener('focusin', handleFocusIn);

      //   
      if (previousActiveElement.current) {
        previousActiveElement.current.focus();
      }
    };
  }, [isActive, initialFocus]);

  return ref;
}
