/**
 * @hua-labs/hua/framework - License Types
 * 
 *   
 */

/**
 *   / License type
 */
export type LicenseType = 'free' | 'pro' | 'enterprise';

/**
 *   / License feature
 * 
 *       .
 * Each feature is available only with specific license types.
 */
export type LicenseFeature =
  | 'core' //   ( )
  | 'motion-basic' //   (Free)
  | 'motion-pro' //   (Pro+)
  | 'i18n-basic' //  i18n (Free)
  | 'i18n-pro' //  i18n (Pro+)
  | 'preset-basic' //   (Free)
  | 'preset-pro' //   (Pro+)
  | 'white-labeling'; //   (Enterprise)

/**
 *   / License information
 */
export interface LicenseInfo {
  /**
   *   / License type
   */
  type: LicenseType;
  
  /**
   *   / License validity
   */
  valid: boolean;
  
  /**
   *  () / Expiration date (optional)
   */
  expiresAt?: Date;
  
  /**
   *     / List of available features
   */
  features: LicenseFeature[];
  
  /**
   * /  (Enterprise) / Company/service name (Enterprise)
   */
  companyName?: string;
  
  /**
   * API  (Pro/Enterprise) / API key (Pro/Enterprise)
   */
  apiKey?: string;
}

/**
 *    / License check result
 */
export interface LicenseCheckResult {
  /**
   *    / Validation success
   */
  valid: boolean;
  
  /**
   *   (  ) / Failure reason (if validation failed)
   */
  reason?: string;
  
  /**
   *  () / Expiration date (optional)
   */
  expiresAt?: Date;
}

/**
 *      / Feature to license type mapping
 */
export const FEATURE_LICENSE_MAP: Record<LicenseFeature, LicenseType[]> = {
  'core': ['free', 'pro', 'enterprise'],
  'motion-basic': ['free', 'pro', 'enterprise'],
  'motion-pro': ['pro', 'enterprise'],
  'i18n-basic': ['free', 'pro', 'enterprise'],
  'i18n-pro': ['pro', 'enterprise'],
  'preset-basic': ['free', 'pro', 'enterprise'],
  'preset-pro': ['pro', 'enterprise'],
  'white-labeling': ['enterprise'],
};
