/**
 * @hua-labs/hua/framework - LiveRegion
 * 
 *        
 * Component that announces dynamic state changes to screen reader users
 */

'use client';

import React from 'react';

/**
 * LiveRegion  props
 */
export interface LiveRegionProps {
  /**
   *  
   * Message to announce
   */
  message?: string;
  
  /**
   * Live region politeness 
   * Politeness level of the live region
   * 
   * - 'polite':      ()
   * - 'assertive':  
   * - 'off':  
   * 
   * @default "polite"
   */
  politeness?: 'polite' | 'assertive' | 'off';
  
  /**
   *  CSS 
   * Additional CSS classes
   */
  className?: string;
}

/**
 * LiveRegion 
 * 
 *       .
 * Announces dynamic state changes to screen reader users.
 * 
 * @example
 * ```tsx
 * function MyForm() {
 *   const [message, setMessage] = useState('');
 * 
 *   const handleSubmit = async () => {
 *     setMessage(' ...');
 *     await saveData();
 *     setMessage('!');
 *   };
 * 
 *   return (
 *     <div>
 *       <form onSubmit={handleSubmit}>Form fields</form>
 *       <LiveRegion message={message} />
 *     </div>
 *   );
 * }
 * ```
 * 
 * @param props - LiveRegion props
 * @returns LiveRegion 
 */
export function LiveRegion({
  message,
  politeness = 'polite',
  className,
}: LiveRegionProps): React.JSX.Element {
  // aria-live   
  //    .
  //  useEffect  .

  return (
    <div
      role="status"
      aria-live={politeness}
      aria-atomic="true"
      className={className || 'sr-only'}
    >
      {message}
    </div>
  );
}
